<template>
    <b-container>
        <b-row align-h="center">
            <b-col cols="6">
                <h1>My TODO list</h1>
            </b-col>
        </b-row>
        <b-row align-h="center">
            <b-col cols="6">
                <b-input-group>
                    <b-form-input placeholder="What needs to be done?" v-model="newTask"></b-form-input>
                    <b-input-group-append>
                        <b-btn variant="primary" @click="addTask">Add</b-btn>
                    </b-input-group-append>
                </b-input-group>
            </b-col>
        </b-row>
        <br>
        <b-row align-h="center">
            <b-col cols="6">
                <transition-group name="list-complete" tag="b-list-group">
                    <TaskItem v-for="(task, index) in tasks"
                              v-bind:key="task"
                              class="list-complete-item"
                              :task="task"
                              @remove="removeTask(index)"/>
                </transition-group>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
    import TaskItem from "./TaskItem";

    export default {
        name: "TaskList",
        components: {TaskItem},
        data() {
            return {
                tasks: [{title: 'TO', completed: true}, {title: 'DO', completed: true}, {
                    title: 'LIST',
                    completed: true
                }],
                newTask: ''
            };
        },
        methods: {
            addTask() {
                if (this.newTask) {
                    this.tasks.push({
                        title: this.newTask,
                        completed: false
                    });
                    this.newTask = '';
                }
            },
            removeTask(index) {
                this.tasks.splice(index, 1);
            }
        }
    }
</script>

<style scoped>
    .list-complete-item {
        transition: all 1s;
        display: inline-block;
    }

    .list-complete-enter, .list-complete-leave-to {
        opacity: 0;
        transform: translateX(50px);
    }

    .list-complete-leave-active {
        position: absolute;
    }

</style>