<template>
    <b-list-group-item>
        <b-row align-v="center">
            <b-col>
                <b-form-checkbox class="float-left" v-model="task.completed"> {{ task.title }}</b-form-checkbox>
            </b-col>
            <b-col>
                <b-btn class="float-right" variant="danger" @click="removeTask">
                    <icon name="times"/>
                </b-btn>
            </b-col>
        </b-row>
    </b-list-group-item>
</template>

<script>
    export default {
        name: "TaskItem",
        props: ['task'],
        methods:{
            removeTask(){
                this.$emit('remove');
            },
        },
    }

</script>

